import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { PAGE_ROOM_VIEW, PAGE_SCENE_TABLE } from './route';

type RouteInfo = {
    path: string;
    params: any;
};

@Injectable({
    providedIn: 'root',
})
export class RouterService {
    route$ = new BehaviorSubject<RouteInfo>({
        path: PAGE_SCENE_TABLE,
        params: { name: 'Bedroom 1' },
    });

    constructor() {}

    navigate(path: string, params?: any) {
        this.route$.next({ path, params });
    }

    getCurrentRoute() {
        return this.route$;
    }
}
